@import '../../../stylemodules/main.scss';
.section01-background-container  {
    background:#000000;
    width:100vw;
    height:100vh;
    z-index:200;
    position:relative;
    overflow:hidden;
    transition:all 0.5s;
    @include responsive(T) {
           height:70vh;
    }

    .section01-background-wrapper {
        z-index:-100;
        width:100vw;
        height:100%;
        position:absolute;
        left:0;
        opacity:0.8;
    }
    video {
        object-fit:fill;
        height:100%;
       
    }
    .section01-text-container {
        color:#ffffff;
        position:absolute;
        left:50%;
        top:50%;
        transform:translate(-50%, -50%);
        width:80%;
        min-width:325px;
        max-width:1320px;
        h1{
            font-size:4rem;
            word-break:keep-all;
            width:100%;
            @include responsive(T) {
                font-size:$lg-font-size;
            }
            @include responsive(M) {
                font-size:$lg-font-size-mobile;
            }
            @include responsive(M-S) {
                font-size:$lg-font-size-mobile;
            }
        }
    }
}