@mixin responsive($screen) {
    @if($screen == 'M-S') {
        @media screen and (max-width:$xs-breakpoint) {
            @content;
        }
    }

    @if($screen == 'M-L') {
        @media screen and (max-width:$sm-breakpoint) {
            @content;
        }
    }

    @if($screen == 'T') {
        @media screen and (max-width:$md-breakpoint) {
            @content;
        }
    }

    @if($screen == 'D') {
        @media screen and (max-width:$lg-breakpoint) {
            @content;
        }
    }
}



