@import '../../../stylemodules/main.scss';
$primary: #3F51B5;
$dark-primary: #303F9F;
$light-primary: #C5CAE9;
$text: #FFFFFF;
$primary-text: #212121;
$secondary-text: #757575;
$accent: #FF4081;
.section11-container {
    position: relative;
    
    .section11-content {
        width: 100%;
        position: absolute;
        top: 150px;
        display: flex;
     
        .section11-txt {
          width: 50%;

          h4 {
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 600;
            font-size: 1.5rem;
    
    @include responsive(T) {
      font-size: 1.4rem;
    }
    
    @include responsive(M-L) {
      font-size: 1.3rem;
    }
    
    
            
    
            /* identical to box height, or 140% */
    
            text-align: left;
            letter-spacing: -0.03em;
    
            color: #D0D0D0;
          }
    
          h1 {
            font-family: 'Spoqa Han Sans Neo';
            font-style: normal;
            font-weight: 500;
            font-size: 2.3rem;
    
            /* or 81% */
    
            text-align: left;
            letter-spacing: -0.03em;
    
            color: #FFFFFF;
    
    
    @include responsive(T) {
      font-size: 2rem;
    }
    
    @include responsive(M-L) {
      font-size: 1.7rem;
    }
    
    @include responsive(M-S) {
      font-size: 1.3rem;
    }
          }
    
          p {
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 300;
            font-size: 0.9rem;
    
            word-break: keep-all;
            /* or 140% */
    
            letter-spacing: -0.03em;
            text-align: left;
            color: #BBBBBB;
    
            @include responsive(T) {
              font-size: 1.1rem;
            }
            
            @include responsive(M-L) {
              font-size: 0.8rem;
            }
            
            @include responsive(M-S) {
              font-size: 0.7rem;
            }
          }
        }

        .section11-history-map {

            width: 50%;

          
            height: 10px;

            width: 50%;
            height: 10px;
      
            .timeline {
      
              position: relative;
      
              &::before {
                content: '';
                background: #263C76;
                width: 1px;
                height: 2000px;
                top: -500px;
                position: absolute;
                left: 11.7%;
                transform: translateX(-50%);
              }
            }
      
            .timeline-item {
              width: 100%;
              margin-bottom: 90px;
      
      
      
              .timeline-content {
                left: 20%;
                text-align: left;
      
      
                .date {
                  right: auto;
                  right: 0;
                }
      
      
              }
      
      
              &::after {
                content: '';
                display: block;
                clear: both;
              }
            }
      
      
            .timeline-content {
              position: relative;
              width: 45%;
              border-radius: 4px;
      
      
              h2 {
                font-size:1rem;
                color: #464646;
                margin-top: 0;
                font-weight: 400;
                word-break:keep-all;
      
                @include responsive(D) {
                  font-size: 1rem;
                }
                
                @include responsive(T) {
                  font-size: 0.9rem;
                }
                
                @include responsive(M-L) {
                  font-size: 0.9rem;
                }
                
                @include responsive(M-S) {
                  font-size: 0.8rem;
                };
              }
      
      
            }
      
            .timeline-img {
              cursor: pointer;
              width: 50px;
              height: 50px;
              background: #263C76;
              border-radius: 50%;
              position: absolute;
              display: flex;
              left: 10%;
              margin-left: -15px;
              justify-content: space-around;
              align-items: center;
          
              span {
                font-size:1rem;
                color:#ffffff;
                font-weight:600;
              }
            }
      
            a {
              background: $primary;
              color: $text;
              padding: 8px 20px;
              text-transform: uppercase;
              font-size: 14px;
              margin-bottom: 20px;
              margin-top: 10px;
              display: inline-block;
              border-radius: 2px;
              box-shadow: 0 1px 3px -1px rgba(0, 0, 0, .6);
      
              &:hover,
              &:active,
              &:focus {
                background: darken($primary, 10%);
                color: $text;
                text-decoration: none;
              }
      
            }
      
            .timeline-card {
              padding: 0 !important;
      
              p {
                padding: 0 20px;
              }
      
              a {
                margin-left: 20px;
              }
            }
      
            .timeline-item {
              .timeline-img-header {
                background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, .4)), url('https://picsum.photos/1000/800/?random') center center no-repeat;
                background-size: cover;
              }
            }
      
            .timeline-img-header {
      
              height: 200px;
              position: relative;
              margin-bottom: 20px;
      
              h2 {
                color: $text;
                position: absolute;
                bottom: 5px;
                left: 20px;
      
              }
            }
      
            blockquote {
              margin-top: 30px;
              color: $secondary-text;
              border-left-color: $primary;
              padding: 0 20px;
            }
      
      
            @media screen and (max-width: 768px) {
              .timeline {
                .timeline-content {
                  max-width: 100%;
                  width: auto;
                  margin-left: 30px;
                }
      
                .timeline-item {}
              }
      
      
            }
        }
    }
}