@import '../../../stylemodules/main.scss';

.section02-container {
    display: flex;
    padding:120px 0;
    align-items: center;

    .section02-txt-container {
        display: flex;
        width: 100%;

        @include responsive(T) {
            justify-content: center;
        }

        .section02-txt {
            width: 50%;
            text-align: left;
            word-break: keep-all;
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            @include responsive(T) {
                width: 100%;
            }

            h1 {
                font-family: 'Spoqa Han Sans Neo';
                font-style: normal;
                font-weight: 700;
                font-size: 2.5rem;

                @include responsive(T) {
                    font-size: $lg-font-size-mobile;
                }

                @include responsive(M-L) {
                    font-size: $lg-font-size-mobile;
                }

                @include responsive(M-S) {
                    font-size: $md-font-size-mobile;
                }

            }

            span {
                font-family: 'Spoqa Han Sans Neo';
                font-style: normal;
                font-weight: 300;
                font-size: 18px;
                word-break:keep-all;
                @include responsive(T) {
                    font-size: $md-font-size-mobile;
                }

                @include responsive(M-L) {
                    font-size: $sm-font-size;
                }

                @include responsive(M-S) {
                    font-size: 1rem;
                }

            }

            ul {
                display: flex;
                justify-content: space-between;
                width: 370px;
                padding: 0;
                margin-top: 100px;
                position: relative;
                overflow: hidden;
                @include responsive(T) {
                    width:100%;
                }

                li {
                    background: #ffffff;
                    z-index: 100;
                    p {
                        padding-top: 10px;
                        text-align: center;
                        font-size: $sm-font-size-mobile;

                    }

                    img {
                        z-index: 200;
                    }
                }


                .arrow_container {
                    position: absolute;
                    top: 30px;
                    display: block;
                    background: lightgray;
                    height: 1px;
                    width: 0px;
                    
                  
                    left: 0;

                    .arrow {
                        position: relative;
                        width: 0px;
                        background: lightgray;
                        height: 1px;
                        display: block;
                        transition: all 1s;
                        @include responsive(T) {
                            transition: all 4s;
                        }
                    }

                    .arrow.active {
                        width: 300px;
                        @include responsive(T) {
                            width:1000px;
                        }
                    }

                    .arrow::after {
                        position: absolute;
                        top: -3px;
                        right: 0;
                        content: '';
                        width: 6px;
                        height: 6px;
                        border-top: 1px solid lightgray;
                        border-right: 1px solid lightgray;
                        transform: rotate(45deg);
                    }
                }
            }

        }

        .section02-img {
            width: 50%;

            @include responsive(T) {
                display: none;
            }

            img {
                width: 400px;
                height: 400px;



            }
        }
    }
}