@import '../../../stylemodules/main.scss';
.section04-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding:120px 0;
    .section04-sub-txt {
        font-family: 'Spoqa Han Sans Neo';
        font-style: normal;
        font-weight: 250;
        font-size: 25px;
        letter-spacing: -0.03em;
        text-transform: capitalize;
        color: #6A5CFF;
    }

    .section04-head-txt {
        font-family: 'Spoqa Han Sans Neo';
        font-style: normal;
        font-weight: 500;
        font-size:$lg-font-size-mobile;
        letter-spacing: -0.03em;
        text-transform: capitalize;
        color: #000000;
        @include responsive(M-L) {
            font-size: $lg-font-size-mobile;
        }

        @include responsive(M-S) {
            font-size: $md-font-size-mobile;
        }
    }

    .section04-item-list {
        padding:0;
        @include responsive(M-L) {
            padding:0;
            display:flex;
            flex-wrap:wrap;
            justify-content:space-around;
        }
        li {
            width:200px;
            height:200px;
            width:33%;
            float:left;
            @include responsive(M-L) {
                width:150px;
                height:200px;
            }
            @include responsive(M-S) {
                width:130px;
                height:200px;
            }
            img {
                width:60px;
                height:60px;
                @include responsive(M) {
                    width:40px;
                    height:40px;
                }
            }
            h4{
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 600;
                font-size: 1.2rem;
                line-height: 32px;
                /* identical to box height */
                
                text-transform: capitalize;
                
                color: #7E7E7E;  
            }
            span {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 500;
                font-size: 0.9rem;
                line-height: 21px;
                /* or 140% */
                
                text-align: center;
                text-transform: capitalize;
                
                color: #868686;
            }

        }
    }
}