@import '../../../stylemodules/main.scss';

.section03-container {
    position: relative;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    
    .section03-txt {
        .section03-bg-item1 {
            position:absolute;
            top:40px;
            right:40px;

        }
        .section03-bg-item2 {
            position:absolute;
            bottom:40px;
            left:40px
        }
        img {
            width:100px;
            
        }
        h2 {
            font-family: 'Spoqa Han Sans Neo';
            font-style: normal;
            font-weight: 500;
            color: #ffffff;
            text-align: center;
            letter-spacing: -0.03em;
            font-size:$lg-font-size-mobile;
            word-break:keep-all;
            @include responsive(T) {
                font-size:$md-font-size-mobile;
            }
            @include responsive(M-L) {
                font-size: $md-font-size-mobile;
            }

            @include responsive(M-S) {
                font-size: $sm-font-size-mobile;
            }
        }

        h3 {
            font-family: 'Spoqa Han Sans Neo';
            font-style: normal;
            font-weight: 300;
            color: #ffffff;
            text-align: center;
            letter-spacing: -0.03em;
            word-break:keep-all;
            @include responsive(T) {
                font-size:$sm-font-size-mobile;
            }
            @include responsive(M-L) {
                font-size: $md-font-size-mobile;
            }

            @include responsive(M-S) {
                font-size: $sm-font-size-mobile;
            }
        }

        .section03-stack-list {
            list-style: none;
            padding: 0;
            width: 80%;
            margin: 0 auto;
            @include responsive(T) {
                width:100%;
            }
            @include responsive(M-L) {
                width:80%;
            }

            @include responsive(M-S) {
                width:80%;
            }
            
            li {
                list-style: none;
                font-family: 'Noto Sans KR';
                font-style: normal;
                font-weight: 400;
                margin-left:10px;
                float:left;
                font-size:0.8rem;
                padding:5px 8px;
                background:#455584;
                color:#ffffff;
                border-radius: 20px;
                margin-top:5px;
                
            }
        }
    }



}