@import '../../stylemodules/main.scss';

@mixin headermode($border, $bgcolor, $fontColor, $logoColor) {
    width: 100%;
    height: 71.89px;
    background: $bgcolor;
    position: fixed;
    top: 0;
    z-index: 9999;
    border-bottom: $border;
    transition: all 0.3s;

    .header-inner {
        width: 80%;
        height: 100%;
        margin: 0 auto;
        max-width: 1300px;
        min-width: 325px;
        justify-content: space-between;
        padding: 0px;
        display: flex;
        align-items: center;

        .header-logo {
            width: 130px;
            height: 40px;

            @if $logoColor ==W {
                background: url('../../asset//images/logo_negative.png') center center no-repeat;
            }

            @if $logoColor ==T {
                background: url('../../asset//images/logo_white.png') center center no-repeat;
            }

            background-size: contain;
            background-repeat:no-repeat;
        }

        .header-menu {
            color: $fontColor;
            margin: 0;
            padding: 0;
            list-style: none;

            li {
                @content;
                float: left;
                margin-left: 10px;
                padding: 10px;
                cursor: pointer;
                font-weight: 700;
                border-radius: 10px;
                font-size: 15.82px;
            }

            li:hover {
                color: #000000
            }
        }
    }
}

header.scrolled {
    @include headermode(1px solid #ddd, #ffffff, #000000, W);
}

header.default {
    @include headermode(none, none, #ffffff, T);
}

header.default:hover {
    @include headermode(1px solid #ddd, #ffffff, #000000, W) {
        color: gray;
    }

    ;

}