@import '../../../stylemodules/main.scss';
.section13-container {
    display:flex;
    flex-direction:column;
    height:100%;
    justify-content:space-around;
    .section13-txt-container {
        width:50%;
        min-width:325px;
        margin:0 auto;
        text-align:left;
        h1 {
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 600;
            font-size: $lg-font-size;
            @include responsive(M) {
                font-size:2.3rem;
            }
            /* identical to box height */
            color: #FFFFFF;
        }

        h4 {
            font-family: 'Noto Sans KR';
            font-style: normal;
            font-weight: 300;
            font-size: 1.2rem;
            margin-bottom:43.26px;
            color: #FFFFFF;
            word-break: keep-all;

        }

        span {
            background: #FFFFFF;
            border-radius: 31px;
            padding:10px 20px;
            font-size:1rem;
            font-weight:300;
            cursor:pointer;
        }
    }
}