@import '../../../stylemodules/main.scss';

.section09-container {
    display: flex;
    padding: 100px 0;
    height: 100%;
    flex-direction: column;

    .section09-head-txt {
        font-family: 'Spoqa Han Sans Neo';
        font-style: normal;
        font-weight: 700;
        line-height: 122.5%;
        font-size: 2rem;

        /* identical to box height, or 47px */
        color: #000000;
        @include responsive(D) {
            font-size: 1.8rem;
        }

        @include responsive(T) {
            font-size: 1.5rem;
        }

        @include responsive(M-L) {
            font-size: 1.5rem;
        }

        @include responsive(M-S) {
            font-size: 1rem;
        }
       
    }

    .section09-slide-container {
        margin-top: 100px;

        .section09-slide-item {
            height: 300px;

            div {
                display: flex;
                flex-direction: column;
                align-items: center;

                img {
                    width: 120px;
                    height: 120px;
                }

                h3 {
                    font-size: $md-font-size-mobile;
            
                    @include responsive(T) {
                        font-size: 1.1rem;
                    }
            
                    @include responsive(M-L) {
                        font-size: 0.9rem;
                    }
            
                    @include responsive(M-S) {
                        font-size: 0.8rem;
                    }
                }

                p {
                    text-align: left;
                    font-size: $sm-font-size-mobile;
                    color: gray;
                    @include responsive(T) {
                        font-size: 0.9rem;
                    }
            
                    @include responsive(M-L) {
                        font-size: 0.8rem;
                    }
            
                    @include responsive(M-S) {
                        font-size: 0.8rem;
                    }
                }
            }

        }
    }
}

@include responsive(M-L) {}

@include responsive(M-S) {}

@include responsive(T) {}

@include responsive(D) {
    width: 100%;
}