@import '../../../stylemodules/main.scss';

.section05-container {
    background:#2962B8;
    padding-bottom:100px;
    padding-top:50px;
  
    img {
        width:45px;
        animation: rotateImg 1.5s linear  ;transform-origin: 50% 50%;     
    }
    
    @keyframes rotateImg{
        0% {
            transform: rotate(0);
        }
        100% {
            transform: rotate(-180deg);
        }
    
    }
    .section05-head-txt {
        font-family: 'Poppins';
        font-style: normal;
        font-size:$lg-font-size-mobile;
        font-weight:bold;
        /* or 105% */
        text-align: center;
        letter-spacing: -0.03em;
        color: #FFFFFF;
    }

    .section05-sub-txt {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 400;
        font-size:$sm-font-size;
        line-height: 42px;
        /* or 210% */

        text-align: center;
        letter-spacing: -0.03em;

        color: #FFFFFF;

    }

    .section05-progress-container {
        position:relative;
        margin-top:50px;
        .section05-progress-progress-bar {
            position:absolute;
            left:0;
            width:100vw;
            background: #020066;
            height:3px;
        }
        .section05-progress-progress-bar-complete {
            position:absolute;
            left:0;
            background:#FFFFFF;
            height:3px;
            z-index:300;
            transition:all 1s;
        }
    }
    .section05-process-txt {
        width:80%;
        max-width:1320px;
        margin:0 auto;
        
        .section05-process-list {
            display: flex;
            justify-content: space-around;
            padding: 0;
            position: relative;
            top: 20px;
            li {
                width:10%;
                b {
                    color:#ffffff;
                    font-size:1rem;
                    font-weight:bold;
                }

                h4 {
                    font-size:$sm-font-size-mobile;
                    color:#ffffff;
                    @include responsive(T) {
                       font-size:0.8rem;
                    }
                    @include responsive(M-L) {
                        font-size:0.8rem;
                    }
                 
                }
               p {
                display:flex;
                flex-direction:column;
                span {
                    font-size:0.7rem;
                    color: #ffffff;
                    text-align:left;
                }
               }
            }
        }
    }
}