.section12-container {
    height: 100%;
    .section12-txt-container {
    h3 {
        font-family: 'Noto Sans KR';
        font-style: normal;
        font-weight: 700;
        font-size: 1rem;
        line-height: 64px;
        /* identical to box height, or 256% */
        color: #949494;
    }
    h1 {
        word-break:keep-all;
        font-family: 'Spoqa Han Sans Neo';
        font-style: normal;
        font-weight: 500;
        font-size: 2rem;
        line-height: 42px;
        /* or 81% */

        text-align: center;
        letter-spacing: -0.03em;

        color: #444444;
    }
}
    .section12-img-container {
        padding:0;
        width:100%;
        display:flex;
        flex-wrap:wrap;
        li {
            width:25%;
            margin-top:20px;
            img {
                width:60%;
            }
        }
    }
}